
import Vue from 'vue'
import {router} from '@/common/router.js'


import $common from '@/common/common.js'
import $api from '@/common/api.js'
import {
	req
} from "@/common/request.js"

req.baseuUrl = "http://192.168.0.6:80";
req.defaultReq.url = req.baseuUrl + '/api/';
let USERINFO = $common.getUserInfo();
function setReqBaseDaata(){
	USERINFO = $common.getUserInfo()
	req.defaultReq.type = "POST";
	let user_id = "",user_token = "";
	if(USERINFO){
		user_id =  USERINFO.id;
		user_token =  USERINFO.token;
	}
	req.defaultReq.baseData = {
		"user_id": user_id,
		"token":user_token,



	};
	req.defaultReq.header = {
		'Content-Type': 'application/x-www-form-urlencoded;charset=UTF-8'
	}
}
setReqBaseDaata()

req.defaultReq.beforeSend = (request) => {
	USERINFO = $common.getUserInfo();
	if(!request.filter){
		return request;
	}else{
		if (USERINFO) {
			setReqBaseDaata()
			return request;
		} else {
			let pages = getCurrentPages();
			let page = pages[pages.length - 1];
			for (let key in router.routers) {
				for (let i = 0; i < router.routers[key].length; i++) {
					let {name,path} = router.routers[key][i];
					if(path=="/"+page.route){
						router.push({"name":"login",params:{"from":name}})
					}
				}
			}
		}
	}
}
async function myAjax(options){
	let defaults = Object.assign({
		path:"",
		title:"",
		data:{}
	},options);
	try{
		const res = await req.ajax({
			path: defaults.path,
			title: defaults.title,
			data:defaults.data,
			abortFun: (info,bt) => {
				if(defaults.filter&&defaults.filter()){
					bt.abort();
				}
			}
		})
		let {data,code,msg} = res.data;
		if(code==2000){
			defaults.success&&defaults.success(res)
		}else{
			uni.showToast({
				title: msg,
				icon: 'none',
				success() {
					
				}
			});
		}
	}catch(e){
		console.log(e);
		let res = e.description?e.description:e;
		let { data } = res;
		if(data.code==401){
			uni.showToast({
				title: data.msg,
				icon: 'none',
				success() {
					setTimeout(function() {
						uni.removeStorageSync('USERINFO')
						uni.hideToast()
						appLaunch();
					}, 1200);
				}
			});
			return false;
		}
		defaults.fail&&defaults.fail(e)
	}
}


router.beforeEach((to, from, next) => {
	USERINFO = $common.getUserInfo()
	if (!to.filter) {
		if (!USERINFO) {
			next({
				name: 'login',
				NAVTYPE: 'push',
				 params: {
					"from": from.name
				}
			});
			return;
		} else {
			setReqBaseDaata()
		}
	} else {
		
	}
	next()
})


function appLaunch(){ // 强制登录
	USERINFO = $common.getUserInfo()
	if(!USERINFO){
		//非NVUE使用路由跳转登录

			router.replaceAll({"name":"login"})

		//NVUE使用uni-app跳转登录





	}
}
module.exports.$common = $common;
module.exports.$api = $api;
module.exports.$req = req;
module.exports.$ajax = myAjax;
module.exports.$appLaunch = appLaunch;
Vue.prototype.$common = $common;
Vue.prototype.$api = $api;
Vue.prototype.$req = req;
Vue.prototype.$ajax = myAjax;
Vue.prototype.$appLaunch = appLaunch;







